<!--  -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="task" ref="taskForm" :rules="rules" label-width="100px" class="edit-form">
      <el-form-item label="" prop="picture">
        <sui-photo :url="task.picture" @refreshPhoto="refreshPhoto" :disabled="task.taskStatus!=''&&!task.taskStatus==1"></sui-photo>
      </el-form-item>
      <el-form-item label="" prop="taskTitle">
        <el-input v-model="task.taskTitle" placeholder="" :disabled="task.taskStatus!=''&&!task.taskStatus==1" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="taskGrade">
        <el-input v-model="task.taskGrade" placeholder="" :disabled="task.taskStatus!=''&&!task.taskStatus==1" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="content" >
        <el-input type="textarea" v-model="task.content" placeholder="" :disabled="task.taskStatus!=''&&!task.taskStatus==1"/>
      </el-form-item>
<!--      <el-form-item label="" prop="fileList">-->
<!--        <sui-file @refreshFile="refreshFile" :refId="task.id" :disabled="dialog.disabled"></sui-file>-->
<!--      </el-form-item>-->
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel"> </el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('taskForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import suiPhoto from "@/components/sui-photo/sui-photo";
  import suiFile from "@/components/sui-file/sui-file";
  export default {
    components: {
      suiFile,suiPhoto,
    },
    name: "taskForm",
    data() {
      return {
        sysUserDict:[],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        task: {
          fileList:[],
          picture: "",
          taskTitle: "",
          taskGrade: "",
          publishUserId: "",
          publishTime: "",
          taskStatus: "",
          taskType: "",
          content: ""
        },
        //
        rules: {
          picture: [{required: true, message: '', trigger: 'change'}],
          taskTitle: [{required: true, message: '', trigger: 'blur'}],
          taskGrade: [{required: true, message: '', trigger: 'blur'}],
        }
      }
    },
    computed: {
      //
      taskStatusDict() {
        return this.$store.getters.getDictArray('info_status');
      },
      //
      taskTypeDict() {
        return this.$store.getters.getDictArray('task_type');
      },
    },
    methods: {
      //
      refreshPhoto(url){
        this.task.picture = url;
      },
      //
      getSysUserDict(){
        this.$request.doPost('/api/sysUser/getList', {}).then(res => {
          if (res.isOk) {
            this.sysUserDict=res.list;
          }
        })
      },
      //
      refreshFile(fileList){
        this.task.fileList = fileList;
      },
      //
      handlePublishXun(title) {
        this.showDialog(title, false);
        //
        this.task.taskType=1
        this.getSysUserDict();
      },
      handlePublishZhao(title) {
        this.showDialog(title, false);
        this.task.taskType=2
        //
        this.getSysUserDict();
      },
      //
      openDialogView(title,url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, true);
            this.task = res.obj;
            //
            this.getSysUserDict();
          }
        });
      },
      //
      openDialogEdit(title, url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, false);
            this.task = res.obj;
            //
            this.getSysUserDict();
          }
        });
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/task/clientSub', that.task).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.task);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>
</style>



