<!--  -->
<template>
  <el-dialog  width="500px" center :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysUser" ref="sysUserForm" :rules="rules" label-width="100px">
      <el-form-item label="" prop="loginName">
        <el-input v-model="sysUser.loginName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="sysUser.password" type="password" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel"> </el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="login('sysUserForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import suiPhoto from "./../../../../../components/sui-photo/sui-photo";
  import {mapActions} from "vuex";
  export default {
    components: {suiPhoto},
    name: "sysUserForm",
    data() {
      return {
        roleList: [],
        checkRoleList: [""],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysUser: {
          loginName: "",
          password: ""
        },
        //
        rules: {
        }
      }
    },
    mounted() {
    },
    methods: {
      ...mapActions(['commitConfig', 'commitDictList', 'commitNoticeList', 'commitToken', 'commitLoginInfo']),
      initData() {
        const loading = this.$loading({
          lock: true,
          text: ',...',
          spinner: 'el-icon-loading',
          background: 'rgba(0,0,0,0.25)'
        });
        //
        this.commitConfig().then(res => {
          //
          this.commitDictList().then(res => {
            //
            this.commitLoginInfo().then(res => {
              loading.close();
            })
          })
        })
      },
      login(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {//
            this.$request.doPost("/api/login/userLogin", this.sysUser).then(res => {
              if (res.isOk) {
                this.$emit('reloadList');
                this.initData();
                this.dialog.open = false;
                this.showSuccess(res.info);
              } else {
                this.showWarn(res.info);
              }
            });
          }
        });
      },
      refreshPhoto(url){
        this.sysUser.photo = url;
      },
      getRoleList(userId){
        const that = this;
        that.$request.doGet('/api/sysUser/findRolesByUserId?userId='+userId).then(res => {
          if (res.isOk) {
            that.roleList = res.list;
            this.checkRoleList = that.roleList.filter(function (v) {
              if (v.checked) {return v}
            });
          }
        });
      },
      //
      openDialogLogin(title) {
        this.showDialog(title, false);
      },
      //
      openDialogView(title,url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, true);
            this.sysUser = res.obj;
            this.getRoleList(this.sysUser.id)
          }
        });
      },
      //
      openDialogEdit(title, url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, false);
            this.sysUser = res.obj;
            this.getRoleList(this.sysUser.id)
          }
        });
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            if(that.checkRoleList.length==0){
              that.showWarn("");
              return false
            }
            that.sysUser.roleList=that.checkRoleList.map(function (v) {
              return v.roleId
            });
            that.$request.doPost('/api/login/updateUserInfo',that.sysUser).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.sysUser);
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>

<style scoped>
</style>



