<!--  -->
<template>
    <el-dialog center :title="dialog.title" :visible.sync="dialog.open">
        <el-form :model="claim" ref="claimForm" :rules="rules" label-width="100px" class="edit-form">
<!--            <el-form-item label="" prop="claimStatus">-->
<!--                <el-select v-model="claim.claimStatus" placeholder="" :disabled="dialog.disabled" >-->
<!--                    <el-option v-for="dict in claimStatusDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>-->
<!--                </el-select>-->
<!--            </el-form-item>-->
<!--            <el-form-item label="" prop="claimDate">-->
<!--                <el-date-picker v-model="claim.claimDate" placeholder="" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd" />-->
<!--            </el-form-item>-->
            <el-form-item label="" prop="claimGoods">
                <el-input v-model="claim.claimGoods" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
            </el-form-item>
            <el-form-item label="" prop="userName">
                <el-input v-model="claim.userName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
            </el-form-item>
            <el-form-item label="" prop="userPhone">
                <el-input v-model="claim.userPhone" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
            </el-form-item>
            <el-form-item label="" prop="remarks" >
                <el-input type="textarea" v-model="claim.remarks" placeholder="" :disabled="dialog.disabled"/>
            </el-form-item>
                <el-form-item label="" prop="fileList">
                    <sui-file @refreshFile="refreshFile" :refId="claim.id" :disabled="true"></sui-file>
                </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="cancel"> </el-button>
            <el-button size="small" type="primary" v-if="!dialog.disabled" @click="submitForm('claimForm')"></el-button>
        </div>
    </el-dialog>
</template>

<script>
    import suiFile from "@/components/sui-file/sui-file";
    export default {
        components: {
            suiFile,
        },
        name: "claimForm",
        data() {
          return {
            taskId:"",
            taskDict:[],
            //
            dialog: {
                title: "",
                open: false,
                disabled: false
            },
            //
            claim: {
                fileList:[],
                id: "",
                claimStatus: "",
                claimDate: "",
                taskId: "",
                claimGoods: "",
                userName: "",
                userPhone: "",
                remarks: ""
            },
            //
            rules: {
                claimStatus: [{required: true, message: '', trigger: 'change'}],
                claimDate: [{required: true, message: '', trigger: 'change'}],
                taskId: [{required: true, message: '', trigger: 'change'}],
                claimGoods: [{required: true, message: '', trigger: 'blur'}],
                userName: [{required: true, message: '', trigger: 'blur'}],
                userPhone: [{required: true, message: '', trigger: 'blur'}],
            }
          }
        },
        computed: {
            //
            claimStatusDict() {
                return this.$store.getters.getDictArray('claim_status');
            },
        },
        methods: {
            //
            getTaskDict(){
                this.$request.doPost('/api/task/getList', {}).then(res => {
                    if (res.isOk) {
                        this.taskDict=res.list;
                    }
                })
            },
            //
            refreshFile(fileList){
                this.claim.fileList = fileList;
            },
            //
            openDialogAduit(title,url) {
              this.$request.doGet(url).then(res => {
                if (res.isOk) {
                  this.showDialog(title, false);
                  this.claim = res.obj;
                  this.taskId=res.obj.taskId
                }
              });
            },
          //
            openDialogRenling(title,id) {
                this.id=id;
                this.showDialog(title, false);
            },
            openDialogAduit2(title,url) {
              this.$request.doGet(url).then(res => {
                if (res.isOk) {
                  this.showDialog(title, true);
                  this.claim = res.obj;
                  this.taskId=res.obj.taskId
                }
              });
            },
            //
            cancel: function () {
                this.dialog.open = false
            },
            //
            submitForm: function (formName) {
              const that = this;
              that.claim.taskId = this.taskId;
                that.$refs[formName].validate((valid) => {
                    if (valid) {//
                        that.$request.doPost('/api/claim/sub', that.claim).then(res => {
                            if (res.isOk) {
                                that.dialog.open = false;
                                that.showSuccess("");
                                that.$emit('reloadList');
                            }else{
                              that.showWarn(res.info);
                            }
                        });
                    }
                });
            },
            //
            showDialog(title, disabled) {
                this.resetForm(this.claim);//
                this.dialog.open = true;//
                this.dialog.title = title;//
                this.dialog.disabled = disabled;//
            }
        }
    }
</script>

<style scoped>
</style>



