<!--  -->
<template>
  <el-dialog width="500px" center :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysUser" ref="sysUserForm" :rules="rules" label-width="100px">
      <el-form-item label="" prop="loginName">
        <sui-photo @refreshPhoto="refreshPhoto" v-model="sysUser.photo" :disabled="dialog.disabled" :url="sysUser.photo">11</sui-photo>
      </el-form-item>
      <el-form-item label="" prop="loginName">
        <el-input v-model="sysUser.loginName" placeholder="" :disabled="true" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="userName">
        <el-input v-model="sysUser.userName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="sysUser.phone" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="sysUser.email" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel"> </el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysUserForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import suiPhoto from "./../../../../../components/sui-photo/sui-photo";
  export default {
    components: {suiPhoto},
    name: "sysUserForm",
    data() {
      return {
        roleList: [],
        checkRoleList: [""],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysUser: {
          photo: "",
          loginName: "",
          password: "",
          userName: "",
          userNo: "",
          email: "",
          phone: "",
          officeId: ""
        },

        //
        rules: {
          email: [{type: 'email', message: '', trigger: ['blur', 'change']}],
          userName: [{validator:this.$validator.chineseName}],
          userNo: [{validator:this.$validator.idCard}],
          phone: [{validator:this.$validator.phone}],
        }
      }
    },
    mounted() {
    },
    methods: {
      refreshPhoto(url){
        this.sysUser.photo = url;
      },
      getRoleList(userId){
        const that = this;
        that.$request.doGet('/api/sysUser/findRolesByUserId?userId='+userId).then(res => {
          if (res.isOk) {
            that.roleList = res.list;
            this.checkRoleList = that.roleList.filter(function (v) {
              if (v.checked) {return v}
            });
          }
        });
      },
      //
      openDialogAdd(title) {
        this.getRoleList()
        this.showDialog(title, false);
      },
      //
      openDialogView(title,url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, true);
            this.sysUser = res.obj;
            this.getRoleList(this.sysUser.id)
          }
        });
      },
      //
      openDialogEdit(title, url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, false);
            this.sysUser = res.obj;
            this.getRoleList(this.sysUser.id)
          }
        });
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            that.sysUser.roleList=that.checkRoleList.map(function (v) {
              return v.roleId
            });
            that.$request.doPost('/api/login/updateUserInfo',that.sysUser).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.sysUser);
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>

<style scoped>
</style>



